import { Injectable } from '@angular/core';
import {Http} from '@angular/http';

var FIND_ALL = "/tasks";

@Injectable()
export class TaskService {

  http: Http;

  constructor(http:Http) {
    this.http = http
  }

  findAll() {
    return this.http
      .get(FIND_ALL, {})
  }
}



